import React, { Component } from 'react';
import { Layout, Menu } from 'antd';
import './App.css';
import 'semantic-ui-css/semantic.min.css';
import { HashRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Home from "./pages/Home";
import Genxin from './pages/Genxin';
import KnowledgeBase from './pages/KnowledgeBase';
import Warning from './pages/warning';
import HBhome from './pages/ST/HongBao/HBhome';
import HBgive from './pages/ST/HongBao/HBgive';
import STAirdrop from './pages/ST/STAirdrop';
import HangQin from './pages/HangQin/HangQin';
import Hangqinadd from './pages/HangQin/Hangqinadd';
import LGCtransfer from './pages/main/LGCtransfer';
import Message from './pages/ST/message';
import Test from './pages/ST/message/test3'
import MsgSent from './pages/ST/message/sent';
import MessageNew from './pages/ST/message/new';
import TokenFactory from './pages/ST/TokenFactory/TokenFactory';
import TkFactoryDDXH from './pages/ST/TokenFactory/TkFactoryZBXH';
import TkFactoryHome from './pages/ST/TokenFactory/TkFactoryHome';
import LockIndex from './pages/ST/Lock/LockIndex';
import Lockhome from './pages/ST/Lock/LockHome';
import LockMy from './pages/ST/Lock/LockMy';
import LockCreat from './pages/ST/Lock/LockCreat';
import LockhomePersonal from './pages/ST/Lock/Lock_personal/LockHome';
import LockMyPersonal from './pages/ST/Lock/Lock_personal/LockMy';
import LockCreatPersonal from './pages/ST/Lock/Lock_personal/LockCreat';
import Super from './pages/Admin/Super';
import Admin from './pages/Admin/admin';
import Node from "./pages/node";
import Rukou from "./pages/Rukou/rukou";
import Edu from "./pages/edu";
import TBTC from "./pages/edu/BTC/tunBTC";
import TBTCa0 from "./pages/edu/BTC/tunBTC/TBTCa0";
import TBTCa1 from "./pages/edu/BTC/tunBTC/TBTCa1";
import TBTCa2 from "./pages/edu/BTC/tunBTC/TBTCa2";
import TBTCa3 from "./pages/edu/BTC/tunBTC/TBTCa3";
import TBTCa4 from "./pages/edu/BTC/tunBTC/TBTCa4";
import TBTCa5 from "./pages/edu/BTC/tunBTC/TBTCa5";
import TBTCa6 from "./pages/edu/BTC/tunBTC/TBTCa6";
import PlayToken from "./pages/main/playToken";
import LPToken from "./pages/main/playToken/LP";
import Foot from "./components/foot";
import TBTCc3 from "./pages/edu/BTC/tunBTC/TBTCc3";
import Price from './pages/HangQin/price';
import Price2 from './pages/HangQin/price2';
import NFTEX from './pages/ST/NFTEX';


const { Footer, Content } = Layout;

class App extends Component {
  state = {
    classSelect: "",
  };

  async componentDidMount() {
    if (typeof window.ethereum !== 'undefined') {
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try {
        //第一次链接Metamask
        const accounts = await ethereum.enable()

        //初始化Provider
        const provider = window['ethereum']

        if (provider.chainId === '0x1') {
          this.setState({ chainId: "以太链" });
          this.setState({ disabled: "disabled" })
        };

        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        window.user = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts) {
          console.log("accountsChanged:" + accounts)
        })

        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion) {
          console.log(networkVersion)
        })
      } catch (e) { }
    } else { console.log('没有metamask') }

    var system = {};
    var p = navigator.platform;
    system.win = p.indexOf("Win") === 0;
    system.mac = p.indexOf("Mac") === 0;
    system.x11 = (p === "X11") || (p.indexOf("Linux") === 0);
    if (system.win || system.mac || system.xll) {
      //如果是电脑,添加ui container
      this.setState({ classSelect: "ui container" })
    } else {  //如果是手机,不添加container
      this.setState({ classSelect: "0" });
    }

  }

  refresh = () => {
    this.componentDidMount()
  }

  render() {

    return (

      <div className={this.state.classSelect}>
        <Layout style={{ borderRadius: '10px', background: 'linear-gradient(rgba(24, 144, 255,0.25),rgba(24, 144, 255,0.05))' }}>
          <Content className="site-layout" style={{ padding: '0 2px', marginTop: 2 }}>
            <div className="site-layout-background" style={{ padding: 1, minHeight: 660 }}>
              <Router>
                <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}
                  style={{ borderRadius: '10px', background: '#001e78' }}
                >
                  <Menu.Item style={{ borderRadius: '7px' }} key="1"><Link to="/" style={{ color: '#ffffffe6' }}>主页</Link></Menu.Item>
                  <Menu.Item style={{ borderRadius: '7px' }} key="3"><Link to="/HangQin" style={{ color: '#ffffffe6' }}>行情</Link></Menu.Item>
                  <Menu.Item style={{ borderRadius: '7px' }} key="4"><Link to="/rukou" style={{ color: '#ffffffe6' }}>入口</Link></Menu.Item>
                  <Menu.Item style={{ borderRadius: '7px' }} key="5"><Link to="/warning" style={{ color: '#ffffffe6' }}>声明</Link></Menu.Item>
                  <Menu.Item style={{ borderRadius: '7px' }} key="6"><Link to="/Genxin" style={{ color: '#ffffffe6' }}>通知</Link> </Menu.Item>

                </Menu>

                <Switch>

                  <Route path="/NFTEX" component={NFTEX} />
                  <Route path="/test" component={Test} />
                  <Route path="/edu" component={Edu} />
                  <Route path="/TBTC" component={TBTC} />
                  <Route path="/TBTCa0" component={TBTCa0} />
                  <Route path="/TBTCa1" component={TBTCa1} />
                  <Route path="/TBTCa2" component={TBTCa2} />
                  <Route path="/TBTCa3" component={TBTCa3} />
                  <Route path="/TBTCa4" component={TBTCa4} />
                  <Route path="/TBTCa5" component={TBTCa5} />
                  <Route path="/TBTCa6" component={TBTCa6} />
                  {/* <Route path="/TBTCb1" component={TBTCb1} />
                          <Route path="/TBTCb2" component={TBTCb2} />
                          <Route path="/TBTCb3" component={TBTCb3} />
                          <Route path="/TBTCb4" component={TBTCb4} />
                          <Route path="/TBTCb5" component={TBTCb5} />
                          <Route path="/TBTCb6" component={TBTCb6} /> */}
                  {/* <Route path="/TBTCc1" component={TBTCc1} />
                          <Route path="/TBTCc2" component={TBTCc2} /> */}
                  <Route path="/TBTCc3" component={TBTCc3} />
                  <Route path="/Price" component={Price} />
                  <Route path="/Price2" component={Price2} />
                  <Route path="/LPToken" component={LPToken} />
                  <Route path="/PlayToken" component={PlayToken} />
                  <Route path="/rukou" component={Rukou} />
                  <Route path="/admin" component={Admin} />
                  <Route path="/Super" component={Super} />
                  <Route path="/LockIndex" component={LockIndex} />
                  <Route path="/Lockhome" component={Lockhome} />
                  <Route path="/LockMy" component={LockMy} />
                  <Route path="/LockCreat" component={LockCreat} />
                  <Route path="/LockhomePersonal" component={LockhomePersonal} />
                  <Route path="/LockMyPersonal" component={LockMyPersonal} />
                  <Route path="/LockCreatPersonal" component={LockCreatPersonal} />
                  <Route path="/TkFactoryDDXH" component={TkFactoryDDXH} />
                  <Route path="/TkFactoryHome" component={TkFactoryHome} />
                  <Route path="/TokenFactory" component={TokenFactory} />
                  <Route path="/messageNew/:to" component={MessageNew} />
                  <Route path="/messageHome" component={Message} />
                  <Route path="/MsgSent" component={MsgSent} />
                  <Route path="/LGCtransfer" component={LGCtransfer} />
                  <Route path="/Hangqinadd" component={Hangqinadd} />
                  <Route path="/HangQin" component={HangQin} />
                  <Route path="/STAirdrop" component={STAirdrop} />
                  <Route path="/hongbao" component={HBhome} />
                  <Route path="/HBgive" component={HBgive} />
                  <Route path="/KnowledgeBase" component={KnowledgeBase} />
                  <Route path="/warning" component={Warning} />
                  <Route path="/Genxin" component={Genxin} />
                  <Route path="/" exact component={Home} />

                </Switch>
              </Router>
            </div>
            <Footer style={{ textAlign: 'center', padding: '5px 50px', borderRadius: '10px', background: 'rgba(0,0,0,0)' }}>
              <Node />
              <Foot />
            </Footer>
          </Content>
        </Layout>
      </div>

    );
  }
}

export default App;
